.spinach__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 3rem;
}

.spinach__navbar-links {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.spinach__navbar-links_logo {
    margin-right: 2rem;
}

.spinach__navbar-links_logo img {
    width: 115px;
    height: 75px;
}

.spinach__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.spinach__navbar-links_container p,
.spinach__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}


.spinach__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.spinach__navbar-menu svg {
    cursor: pointer;
}

.spinach__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.spinach__navbar-menu_container p {
    margin: 1rem 0;
}


@media screen and (max-width: 1050px) {
    .spinach__navbar-links_container {
        display: none;
    }

    .spinach__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .spinach__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .spinach__navbar {
        padding: 2rem;
    }


    .spinach__navbar-menu_container {
        top: 20px;
    }

}