.spinach__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.spinach__features-container__feature-title{
    display: flex;
    max-width: 180px;
    flex-direction: column;
    align-items: center;
}

.spinach__features-container__feature-title h1{
    font-size: 18px;
    line-height: 24px;
    font-weight: 800;
    font-family: var(--font-family);
    letter-spacing: -0.04em;
    color: #ffffff;
    text-align: center;
}

.spinach__features-container__feature-title div{
    width: 64px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.spinach__features-container__feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;
}
.spinach__features-container__feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #ffffff;
}

@media screen and (max-width: 550px) {
    .spinach__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }
    .spinach__features-container__feature-title p {
        font-size: 12px;
        line-height: 20px;
    }
    .spinach__features-container__feature {
        margin: 1rem 0;
    }
}