.spinach__blog {
    display: flex;
    flex-direction: column;    
}

.spinach__blog-heading {
    width: 100%;
    display: flex;
    margin-bottom: 5rem;
    flex-direction: column;
    align-items: center;
}

.spinach__blog-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}

.spinach__blog-container {
    display: flex;
    flex-direction: row;
}

.spinach__blog-container-groupA {
    flex: 0.75;
    margin-right: 2rem;
}

.spinach__blog-container-groupB {
    flex: 1;

    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}
.spinach__blog-heading div {
    width: 64px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;
    margin-top: 2rem;
}
@media screen and (max-width: 990px) {
    .spinach__blog-container {
        flex-direction: column-reverse;
    }


    .spinach__blog-container-groupA {
        margin: 2rem 0;
    }

    .spinach__blog-container-groupA .spinach__blog-container_article  {
       width: 48%;
    }

    .spinach__blog-container-groupA .spinach__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .spinach__blog-container-groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .spinach__blog-container-groupA .spinach__blog-container_article  {
       width: 100%;
    }

    .spinach__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .spinach__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}